import React from "react";

function Icon() {
    return (
        <svg
            xmlns="http://www.w3.org/2000/svg"
            xmlnsXlink="http://www.w3.org/1999/xlink"
            width="75"
            height="75"
            fill="none"
            viewBox="0 0 674 642"
            id={'logo'}
        >

            <path fill="url(#pattern0)" d="M85 72H585V572H85z"></path>

            <defs>
                <pattern
                    id="pattern0"
                    width="1"
                    height="1"
                    patternContentUnits="objectBoundingBox"
                >
                    <use transform="scale(.002)" xlinkHref="#B"></use>
                </pattern>
                <image
                    id="B"
                    width="500"
                    height="500"

                    xlinkHref="data:image/png;base64,"
                ></image>
            </defs>
                    <path
                        stroke="#fff"
                        strokeWidth="38"
                        d="M655 321c0 165.93-141.492 302-318 302S19 486.93 19 321 160.492 19 337 19s318 136.07 318 302z"
                        id={'clip0'}
                    ></path>
        </svg>
    );
}

export default Icon;
